<script>
  import Home from '../screens/Home.svelte'
  import Login from './Login.svelte'
  import { onDestroy } from 'svelte'
  import { loginOb } from '../services/store'

  let loginStatus = false

  const unsub = loginOb.subscribe((val) => {
    loginStatus = val
  })

  onDestroy(unsub)
</script>

{#if loginStatus} 
  <Home />
{:else} 
  <Login />
{/if}