import { writable, derived } from 'svelte/store'
import { io } from 'socket.io-client'

export const myUsername = writable("")

const loginObject = () => {
  const { subscribe, set, update } = writable(false)

  return {
    subscribe,
    updateLogin: (status) => {
      set(status)
      localStorage.setItem("loggedIn", `${status}`)
    }
  }
}

export const messages = writable({
  "Lounge": []
})

export const updateMessages = (username, room, message) => {
  messages.update(messages => ({
    ...messages,
    [room]: [...messages[room], { username, message }]
  }))
}

export const loginOb = loginObject()

export const socket = io("/")

socket.on("broadcast-msg", (data) => {
  const { username, message } = data
  updateMessages(username, "Lounge", message)
})