<script>
  import ChatContainer from '../components/ChatContainer.svelte'
  import Card from "../components/Card.svelte"
  import Container from "../components/Container.svelte"
  import { myUsername, loginOb, socket, messages, updateMessages } from "../services/store"
  import { onMount } from 'svelte'
  import { onDestroy } from 'svelte/internal';

  let msgs
  let message = ""
  let sub
  let inputRef

  function logout() {
    loginOb.updateLogin(false)
  }
  
  function sendMessage() {
    if (message) {
      socket.emit("new-msg", { username: $myUsername, message })
      updateMessages($myUsername, "Lounge", message)
      message = ""
      inputRef.focus()
    }
  }

  onMount(() => {
    sub = messages.subscribe((val) => {
      msgs = val["Lounge"]
    })

    socket.emit("send-welcome-msg", $myUsername)

    inputRef.focus()
  })

  onDestroy(() => {
    sub()
  })
</script>

<Container>
  <Card>
    <div class="home-header">
      <div class="home-title">The Lounge</div>
      <button class="leave-btn" on:click={logout}>Leave</button>  
    </div>
    <ChatContainer messages={msgs} />
    <div class="form-container">
      <input
        bind:this={inputRef} 
        bind:value={message} 
        on:keypress={(e) => {
          if (e.key === "Enter") sendMessage()
        }}
        class="send-msg-input" 
        placeholder="Enter a message"
      >
      <button 
        class="send-btn" 
        on:click={sendMessage}
      >Send</button>
    </div>
  </Card>
</Container>

<style>
  .home-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .home-title {
    font-weight: 600;
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
  }

  .leave-btn {
    font-weight: 700;
    color: white;
    background-color: #FFC107;
    border-radius: 0.2rem;
    border: 0;
    padding: 0.2rem 1rem;
    margin-right: 1rem;
  }

  .form-container {
    margin: 1rem 0;
  }

  .send-msg-input {
    padding: 0.5rem 1rem;
    border: 0;
    width: 90%;
    box-shadow:0 0 15px 4px rgba(0, 0, 0, 0.15);
  }

  .send-btn {
    font-weight: 700;
    color: white;
    background-color: #FFC107;
    border-radius: 0.2rem;
    border: 0;
    padding: 0.5rem 1rem;
  }
</style>