<script>
  import { afterUpdate, onMount } from 'svelte'
  export let messages
  let chatContainer

  afterUpdate(() => {
    chatContainer.scrollTo(0, chatContainer.scrollHeight)
  })
</script>

<div class="chat-container" bind:this={chatContainer}>
  {#if messages}
    {#each messages as message}
      <div class="message">
        <div class="msg-user">{message.username}</div>
        <div class="msg-msg">{message.message}</div>
      </div>
    {/each}
  {/if}
</div>

<style>
  .chat-container {
    max-height: 400px;
    overflow-y: scroll;
    padding: 0 10px;
  }
  
  .message {
    background-color: white;
    border: 1px solid black;
    margin: 0.3rem 0;
    border-radius: 0.2rem;
    padding: 0 10px;
  }

  .msg-user {
    font-size: 0.8rem;
    font-weight: 600;
  }

  .msg-msg {
    font-size: 0.8rem;
  }
</style>