<script>
  import { myUsername, loginOb, socket } from '../services/store'
  import { onMount } from 'svelte'

  let inputRef

  onMount(() => {
    inputRef.focus()
  })

  function login() {
    if ($myUsername) {
      loginOb.updateLogin(true)
      socket.emit("new-connection", { username: $myUsername })
    }
  }
</script>

<div class="login-page">
  <div class="login-card">
    <div class="title">
      Chat App
    </div>
    <input 
      type="text" 
      bind:this={inputRef}
      bind:value={$myUsername} 
      on:keypress={(e) => {
        if (e.key === 'Enter') login()
      }}
      placeholder="Username"
    >
    <button 
      on:click={login}
    >Enter</button>
  </div>
</div>

<style>
  .login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(189deg, rgba(255,193,7,1) 0%, rgba(246,199,60,1) 35%, rgba(228,174,10,1) 100%);
  }

  .login-card {
    padding: 2rem 1.5rem;
    border-radius: 0.2rem;
    background-color: white;
    box-shadow:0 0 15px 4px rgba(0, 0, 0, 0.15);
  }

  .title {
    font-weight: 700;
    font-size: 1.2rem;
    text-align: center;
  }

  input {
    padding: 0.5rem 1rem;
    margin-top: 1.5rem;
    border: 0;
    width: 100%;
    box-shadow:0 0 15px 4px rgba(0, 0, 0, 0.15);
  }

  button {
    margin-top: 10px;
    width: 100%;
    font-weight: 700;
    color: white;
    background-color: #FFC107;
    border-radius: 0.2rem;
    border: 0;
    padding: 0.4rem 0;
  }

  button:hover {
    background-color: #FFCA2C;
  }
</style>